// carousel
.carousel-wrapper {
  position: relative;
}
.carousel-index {
  position: absolute;
  list-style: none;
  display: flex;
  z-index: 10;
  left: 45%;
  bottom: 5px;
}
@media (max-width: 991px) {
  .carousel-index {
    left: 40%;
  }
}
@media (max-width: 767px) {
  .carousel-index {
    bottom: -15px;
    left: 35%;
  }
}
@media (max-width: 575px) {
  .carousel-index {
    bottom: -10px;
    left: 30%;
  }
}
.indicator-btn {
  background-color: #fff;
  border-radius: 20px;
  height: 20px;
  width: 20px;
  display: inline-block;
  margin-left: 5px;
  cursor: pointer;
  &.active {
    border: 2px solid #fff;
    background-color: red;
  }
}
@media (min-width: 992px) {
  .indicator-btn {
    height: 15px;
    width: 15px;
  }
}
.carousel-imgs {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  animation: round 25s linear infinite;
}
.carousel-img {
  display: none;
  width: 100%;
  position: relative;
  float: left;
  margin-right: -100%;
  transition: transform 1s ease-in-out;
  z-index: 5;
  &.active,
  &.left,
  &.right {
    display: block;
  }
  &.left {
    transform: translateX(-100%);
  }
  &.right {
    transform: translateX(100%);
  }
  & > img {
    display: block;
    width: 100%;
  }
}
// hot lesson
.hot-lesson-item {
  transition: all 0.3s;
  &:hover {
    box-shadow: 0px 9px 15px 2px rgba(137, 142, 150, 0.25);
    transform: translatey(-5px);
  }
}
// recommend
.indicator-btn-recommend {
  background-color: rgba(85, 85, 85, 0.3);
  border-radius: 10px;
  height: 10px;
  width: 10px;
  display: inline-block;
  margin-left: 5px;
  cursor: pointer;
  &.active {
    background-color: #b81939;
  }
}
.recommend-img {
  border-radius: 50%;
  max-width: 100px;
  max-height: 100px;
}
// showcase
.showcase-brand {
  border-radius: 100%;
  background-color: rgb(200, 45, 73);
  color: #fff;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 1.25rem;
  margin-right: 1.25rem;
}
